<template>
  <div class="detail-content">
    <div class="detail-bread">
      <ul>
        <li>
          <router-link to="/">首页</router-link>
        </li>
        <li> ></li>
        <li>
          <router-link to="/post">论坛</router-link>
        </li>
      </ul>
    </div>
    <div class="article box">
      <div class="avastar"><img :src="author.avastar" alt=""></div>
      <div class="title"><h1>{{title}}</h1></div>
      <div class="article-detail">
        <div class="author">发帖人: <a href="javascript:void(0);"><span>{{author.username}}</span></a></div>
        <div class="timestamp">时间: <span>{{formatTime(post.timestamp)}}</span></div>
        <div class="block" v-show="admin" @click="deletePost()">block</div>
      </div>
      <div class="body"><p>{{post.body}}</p></div>
    </div>

    <div class="comment-list box">
      <ul>
        <li v-for="comment in comments" class="cell">
          <div class="comment-img"><img :src="comment.author_avastar" alt=""></div>
          <div class="comment-body"><p>{{comment.body}}</p></div>
          <div class="article-detail">
            <div class="author">回复人: <a href="javascript:void(0);"><span>{{comment.author_name}}</span></a></div>
            <div class="timestamp">时间: <span>{{formatTime(comment.timestamp)}}</span></div>
            <div class="block" v-show="admin" @click="deleteComment(comment.id)">block</div>
          </div>
        </li>
      </ul>
      <section class="pages-wrap" v-show="commentCount>pagenum">
        <ul class="pagination clearfix">
          <li :class="{'disabled': commentCount<pagenum || prev==null}"><a href="javascript:;"
                                                                           @click="getComments(prev)"> 上一页 </a></li>
          <li :class="{'disabled': commentCount<pagenum || next==null}"><a href="javascript:;"
                                                                           @click="getComments(next)"> 下一页 </a></li>
        </ul>
      </section>
    </div>
    <div class="comment-input box">
      <textarea v-model="comment" placeholder="请表达一下看法"></textarea>
      <button class="s-btn" @click="postComment()">提交</button>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: "post-detail",
    data() {
      return {
        baseUrl: this.GLOBAL.host,
        author: {},
        post: {},
        comments_url: '',
        title: '',
        comments: {},
        comment: '',   //输入的评论
        admin: false,
        pagenum: 20,
        prev: '',
        next: '',
        commentCount: 0
      }
    },
    props: {
      postId: {
        type: String,
        default: '',
      }
    },
    watch: {
      postId: function () {
        this.getPostDetail();
      },
    },
    mounted: function () {
      this.getPostDetail();
      this.admin = eval(sessionStorage.getItem('admin'));
    },
    methods: {
      getPostDetail() {
        if (this.postId != null || this.postId != '') {
          var detail_url = this.baseUrl + '/api/v1/posts/' + this.postId;
          axios.get(detail_url).then((res) => {
            this.author['username'] = res.data['author_name'];
            this.author['url'] = res.data['author_url'];
            this.author['avastar'] = res.data['author_avastar'];
            this.title = res.data.title;
            this.post['body'] = res.data['body'];
            this.post['timestamp'] = res.data['timestamp'];
            this.comments_url = res.data['comments_url'];
            this.getComments();
          }).catch((err) => {

          });
        }
      },
      getComments() {
        if (this.comments_url != '') {
          var comments_url = this.baseUrl + this.comments_url;
          axios.get(comments_url).then((res) => {
            console.log('get comments: ');
            console.log(res);
            this.comments = res.data['comments'];
            this.prev = res.data.prev;
            this.next = res.data.next;
            this.commentCount = res.data.count;
          }).catch((err) => {

          });
        }
      },
      postComment() {
        if (this.comment != '') {
          var commentUrl = this.baseUrl + '/api/v1/posts/' + this.postId + '/comments/';
          axios({
            method: 'POST',
            url: commentUrl,
            data: {'body': this.comment}
          }).then((res) => {
            alert('评论成功!');
            this.getPostDetail();
            this.comment = '';
          }).catch((err) => {
            alert('评论失败,确认是否激活账号或是否登录!');
          });
        } else {
          alert('评论内容不能为空!');
        }
      },
      deletePost() {
        if (confirm("确认是否block这个帖子?")) {
          var deleteUrl = this.baseUrl + '/api/v1/posts/delete/' + this.postId;
          axios({
            method: 'DELETE',
            url: deleteUrl,
          }).then((res) => {
            console.log(res);
            this.$router.push({path: '/post'});
          }).catch((err) => {
            alert('删除帖子时出现错误,确认当前登录用户是否是管理员!');
          });
        }
      },
      deleteComment(comment_id) {
        if (confirm("确认是否block这条评论?")) {
          var deleteUrl = this.baseUrl + '/api/v1/comments/' + comment_id;
          axios({
            method: 'DELETE',
            url: deleteUrl,
          }).then((res) => {
            this.getComments();
          }).catch((err) => {
            alert('删除评论出现错误,确认当前登录用户是否是管理员!');
          });
        }
      },
      formatTime(timestamp) {
        let ti = new Date(timestamp);
        return ti.toLocaleString();
      },
    }
  }
</script>

<style scoped>
  .pages-wrap {
    text-align: center;
  }

  .pagination {
    display: inline-block;
    padding-left: 0;
    margin: 20px 0;
    border-radius: 4px;
  }

  .pagination > li {
    display: inline;
  }

  .pagination > .active > a, .pagination > .active > a:hover, .pagination > .active > span, .pagination > .active > span:hover {
    z-index: 3;
    color: #fff;
    cursor: default;
    background-color: #337ab7;
    border-color: #337ab7;
  }

  .pagination > .disabled > a, .pagination > .disabled > a:hover, .pagination > .disabled > span, .pagination > .disabled > span:hover {
    color: #777;
    cursor: not-allowed;
    background-color: #fff;
    border-color: #ddd;
  }

  .pagination > li > a:hover, .pagination > li > span:hover {
    z-index: 2;
    color: rgba(0, 0, 0, 0.6);
    background-color: #eee;
    border-color: #ddd;
  }

  .pagination > li > a, .pagination > li > span {
    position: relative;
    float: left;
    padding: 6px 12px;
    margin-left: -1px;
    line-height: 1.42857143;
    color: #000;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #ddd;
  }

  /*  分页  */
  a:hover {
    color: #4d5256;
    text-decoration: underline;
  }

  .box {
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, .1);
    border-bottom: 1px solid #e2e2e2;
  }

  .cell {
    padding: 10px;
    font-size: 14px;
    line-height: 120%;
    text-align: left;
    border-bottom: 1px solid #e2e2e2;
  }

  img {
    max-width: 48px;
    max-height: 48px;
  }

  .detail-content {
    font-size: 15px;
    width: 100%;
    postition: relative;
    background-color: #f5f7fc;
  }

  .detail-bread {
  }

  .detail-bread > ul {
    padding: 5px;
  }

  .detail-bread > ul > li {
    float: left;
    margin: 7px;
  }

  .article {
    width: 100%;
    position: relative;
    margin-top: 40px;
    padding: 7px;
    border-bottom: 1px solid #e2e2e2;
  }

  .article .avastar {
    display: inline-block;
    float: right;
  }

  .article .title {
    text-indent: 8px;
    text-align: left;
    padding: 0 0 5px 6px;
  }

  .article .title > h1 {
    font-size: 24px;
  }

  .article-detail {
    display: block;
    width: 100%;
    min-height: 25px;
    margin-top: 10px;
    border-bottom: 1px solid #e2e2e2;
    padding-bottom: 30px;
  }

  .article-detail > * {
    float: left;
    font-size: 14px;
    padding: 0 5px 3px 10px;
  }

  .block {
    padding: 2px;
    background-color: #d8d8d8;
    color: #000;
    font-size: 12px;
    text-decoration: none;
    box-shadow: 0 1px 0 rgba(66, 66, 77, .1);
    border-radius: 8px;
    cursor: pointer;
  }

  .body {
    text-align: left;
    font-size: 14px;
    line-height: 1.6;
    color: #000;
    word-wrap: break-word;
    margin-top: 10px;
  }

  .comment-input {
    width: 100%;
    margin-top: 40px;
    padding: 10px;
  }

  .comment-input > textarea {
    width: 80%;
    min-height: 80px;
  }

  .comment-input > button {
    float: right;
  }

  .comment-list {
    margin-top: 20px;
  }

  .comment-list .comment-img {
    display: inline-block;
    float: left;
  }

  .comment-list .comment-body {
    margin-left: 57px;
    text-align: left;
    text-indent: 8px;
    color: #000;
  }

  .comment-list .article-detail {
    font-size: 12px;
    border: none;
  }

  .comment-list .article-detail > * {
    font-size: 12px;
  }

</style>



