<template>
  <div>
    <div v-if="isLogin">
      <div class="status-page">
        <table cellpadding="0" cellspacing="0" border="0" width="100%">
          <tbody>
          <tr>
            <td width="48" valign="top"><a href="javascript:void(0);"><img
              v-lazy="avastar" class="avatar" border="0"
              align="default" style="max-width: 48px; max-height: 48px;"></a></td>
            <td width="10" valign="top"></td>
            <td width="auto" align="left"><span class="bigger"><a href="">{{username}}</a></span>
            </td>
            <td width="auto" v-show="!confirmed" style="color:#e02614">未激活</td>
          </tr>
          </tbody>
        </table>
        <div class="s-btn" @click="mdShowPost=true">发帖</div>
        <div class="s-btn">查看资料</div>
        <div class="s-btn">我的帖子</div>
        <div class="s-btn" @click="logout()">登出</div>
      </div>
    </div>
    <div v-else>
      <div class="status-page">
        <p class="tips">当前尚未登录</p>
        <a class="s-btn" href="javascript:void(0);" @click="mdShowLogin=true;error=''">登录</a>
        <a class="s-btn" href="javascript:void(0);" @click="mdShowRegister=true">注册</a>
      </div>
    </div>
    <div class="show-code">
      <div>文明发言，礼貌回帖</div>
    </div>
    <modal v-bind:mdShow="mdShowLogin" @close="closeModal">
      <div slot="message" class="input-modal">
        <div class="error-tips" v-show="error">{{error}}</div>
        <input type="email" v-model="email" placeholder="邮箱">
        <input type="password" v-model="password" placeholder="密码">
        <button class="s-btn" @click="login()">确认登录</button>
        <button class="s-btn" @click="forget()">忘记密码</button>
      </div>
    </modal>
    <modal v-bind:mdShow="mdShowRegister" @close="closeModal">
      <div slot="message" class="input-modal">
        <div class="error-tips" v-show="error">{{error}}</div>
        <input type="email" v-model="email" placeholder="邮箱">
        <input type="text" v-model="username" placeholder="用户名">
        <input type="password" v-model="password" placeholder="密码">
        <button class="s-btn" @click="register()">确认注册</button>
      </div>
    </modal>
    <modal v-bind:mdShow="mdShowPost" @close="closeModal">
      <div slot="message" class="input-modal">
        <div class="error-tips" v-show="error">{{error}}</div>
        <input type="text" v-model="post_title" placeholder="标题">
        <textarea v-model="post_body" placeholder="内容"></textarea>
        <select v-model="post_city">
          <option disabled value="">请选择城市:</option>
          <option>深圳</option>
          <option>广州</option>
          <option>上海</option>
          <option>杭州</option>
          <option>南京</option>
        </select>
        <button class="s-btn" @click="topost()">确认提交</button>
      </div>
    </modal>
  </div>

</template>

<script>
  import axios from 'axios'
  import Modal from '@/components/Modal'

  export default {
    name: "user-status",
    data() {
      return {
        isLogin: false,
        email: '',
        username: '',
        password: '',
        avastar: '',
        confirmed:true,
        admin:false,
        error: '',
        post_title: '',
        post_body: '',
        post_city: '',
        baseUrl: this.GLOBAL.host,
        mdShowLogin: false,
        mdShowRegister: false,
        mdShowPost: false,
      }
    },
    components: {
      Modal,
    },
    mounted: function () {
      this.checkUserStatus();
    },
    methods: {
      checkUserStatus() {
        const token = sessionStorage.getItem('token');
        if (token == null) {
          // 用户未登录
          this.isLogin = false;
        } else {
          // 已登录
          this.isLogin = true;
          this.avastar = sessionStorage.getItem('avastar');
          this.username = sessionStorage.getItem('username');
          this.confirmed = eval(sessionStorage.getItem('confirmed'));
          this.admin = eval(sessionStorage.getItem('admin'));
        }
      },
      login() {
        this.mdShowLogin = true;
        this.error='';
        const loginUrl = this.baseUrl + '/api/v1/tokens/';
        axios({
          method: 'POST',
          url: loginUrl,
          auth: {username: this.email, password: this.password}
        }).then((res) => {
          console.log(res);
          for (var key in res.data) {
            sessionStorage.setItem(key, res.data[key]);
          }
          this.avastar = res.data.avastar;
          this.username = res.data.username;
          this.confirmed = res.data.confirmed;
          this.isLogin = true;
          this.mdShowLogin = false;
          this.error = '';
        }).catch((err) => {
          this.error = '用户名或密码错误!';
        })
      },
      register() {
        this.mdShowRegister = true;
        const registerUrl = this.baseUrl + '/api/v1/users/register';
        axios({
          method:'POST',
          url:registerUrl,
          data:{
            email:this.email,
            username:this.username,
            password:this.password
          }
        }).then((res)=>{
          console.log(res);
          if(res.status==201){
            this.mdShowRegister = false;
            this.mdShowLogin = true;
            this.password = '';
            this.error = '注册成功,请前往邮箱激活!';
          }
        }).catch((err)=>{
          this.error = '注册失败,可能邮箱或用户名重复!';
        });
      },
      logout() {
        if (confirm('确认要登出吗?')) {
          sessionStorage.clear();
          this.isLogin = false;
        }
      },
      forget() {
        var url = this.baseUrl + '/api/v1/users/';
        axios.get(url).then((res) => {
          console.log(res);
        }).catch((err) => {

        });
      },
      topost() {
        /*用户发帖*/
        this.error='';
        const city_dict = {'深圳': 'sz', '广州': 'gz', '上海': 'sh', '杭州': 'hz', '南京': 'nj'};
        if (city_dict[this.post_city] != undefined) {
          axios({
            method:'POST',
            url:this.baseUrl+'/api/v1/'+city_dict[this.post_city]+'/posts',
            data:{
              title:this.post_title,
              body:this.post_body
            }
          }).then((res) => {
            this.$emit('tomypost',city_dict[this.post_city]);
            this.mdShowPost = false;
          }).catch((err) => {
            this.error='发帖错误,请检查是否激活账号...';
          });
        } else {
          this.error = '请选择城市';
        }
      },
      closeModal() {
        this.mdShowLogin = false;
        this.mdShowRegister = false;
        this.mdShowPost = false;
        this.error = '';
      }
    },
  }
</script>

<style>
  .status-page {
    padding: 10px;
    min-height: 80px;
    font-size: 14px;
    line-height: 120%;
    text-align: center;
    border-bottom: 1px solid #e2e2e2;
  }
  .show-code{
    line-height:50px;
    text-align: center;
  }

  .tips {
    margin-bottom: 12px;
  }

  .error-tips {
    color: #e02614;
  }

  .s-btn {
    position: relative;
    display: inline-block;
    min-width: 88px;
    height: 36px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0 16px;
    margin: 5px;
    overflow: hidden;
    font-size: 14px;
    font-weight: 500;
    line-height: 36px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: .04em;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    zoom: 1;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background: #42b983;
    border: none;
    border-radius: 2px;
    outline: none;
    -webkit-transition: all .2s cubic-bezier(.4, 0, .2, 1), -webkit-box-shadow .2s cubic-bezier(.4, 0, 1, 1);
    transition: all .2s cubic-bezier(.4, 0, .2, 1), -webkit-box-shadow .2s cubic-bezier(.4, 0, 1, 1);
    transition: all .2s cubic-bezier(.4, 0, .2, 1), box-shadow .2s cubic-bezier(.4, 0, 1, 1);
    transition: all .2s cubic-bezier(.4, 0, .2, 1), box-shadow .2s cubic-bezier(.4, 0, 1, 1), -webkit-box-shadow .2s cubic-bezier(.4, 0, 1, 1);
    will-change: box-shadow;
    -webkit-user-drag: none;
  }

  .s-btn:hover {
    color: #000;
    background-color: rgba(0, 0, 0, .1);
  }

  .input-modal {
    text-align: center;
  }

  .input-modal > *:not(button) {
    margin: 20px auto;
    display: block;
  }

  .input-modal > textarea {
    min-width: 370px;
  }

  .input-modal > select {
    padding: 5px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid #000;
  }

  .input-modal > input {
    margin: 20px auto;
    display: block;
    font-size: 16px;
    font-weight: normal;
    min-width: 370px;
    padding: 0;
    border: none;
    border-bottom: 1px solid #7a7775;
  }



  .avastar > img {
    max-width: 48px;
    max-height: 48px;
  }

  .avastar > span {
    display: inline-block;
    height: 48px;

  }

</style>



