<template>
  <div>
    <nav-header @change="cityChange" :emitCity="city"></nav-header>
    <div class="content">
      <div class="leftBar"></div>
      <div class="rightBar">
        <div class="box">
          <div class="cell">
            <user-status @tomypost="cityChange"></user-status>
          </div>
          <div class="cell"></div>
        </div>
      </div>
      <div class="main box">
        <div class="posts">
          <post-detail :postId="$route.params.postId"></post-detail>
        </div>
      </div>
    </div>
    <nav-footer></nav-footer>
  </div>
</template>

<script>
  import NavHeader from '@/components/NavHeader'
  import NavFooter from '@/components/NavFooter'
  import UserStatus from '@/page/UserStatus'
  import PostDetail from '@/page/PostDetail'

  export default {
    name: "post-detail-page",
    data() {
      return {
        city:'sz',
      }
    },
    components: {
      NavHeader,
      NavFooter,
      UserStatus,
      PostDetail,
    },
    mounted: function () {

    },
    methods: {
      cityChange(city){
        this.city = city;
      },
    }
  }
</script>

<style scoped>
  .content {
    min-width: 600px;
    max-width: 1100px;
    margin: 20px auto 20px auto;
    text-align: center;
  }

  .box {
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, .1);
    border-bottom: 1px solid #e2e2e2;
  }

  .cell {
    padding: 10px;
    font-size: 14px;
    line-height: 120%;
    text-align: left;
    border-bottom: 1px solid #e2e2e2;
  }

  .main {
    width: auto;
    margin: 0 310px 0 20px;
  }

  .main .posts {
    width: 100%;
    min-height:300px;
    margin: 0 auto;
  }

  .leftBar {
    width: 0;
    float: left;
  }

  .rightBar {
    float: right;
    display: inline-block;
    width: 270px;
    margin-right: 20px;
  }
</style>



